import React from 'react'
import styles from './Button.module.css'

const Button = props => {
  const { children, isDark, customStyle } = props
  console.log(styles);
  return (
    <button
      className={`${styles.myButton} ${isDark ? styles.dark : styles.light}`}
      style={customStyle}
    >
      { children }
    </button>
  )
}

export default Button
